import React, { useState } from 'react'
import Dialog from '@material-ui/core/Dialog';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import Typography from '@material-ui/core/Typography';
import SourceViewInfo from '../SourceViewInfo/SourceViewInfo'
import SVBLogo from '../../images/SVLogo.jpeg'

import { makeStyles } from '@material-ui/core/styles'

const useStyles = makeStyles({
  dialogTitle: {
    display: 'flex',
    flexWrap: 'nowrap',
    justifyContent: 'space-between'
  },
  title: {
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center'
  },
  SVicons: {
    height: '50px',
    width: '50px',
    marginRight: '5px',
    marginBottom: '5px'
  },
  sourceName: {
    color: 'rgb(180, 180, 180)',
    marginTop: '10px',
    marginBottom: '0px',
    fontSize: '80%',
    textTransform: 'uppercase',
  },
  dialogContent: {
    display: 'flex',
    justifyContent: 'center'
  }
})

const SourceName = (props) => {
  const classes = useStyles();
    
    // This controls the popup for the source information
    const [ sourceDialog, updateSourceDialog ] = useState({open: false, content: {}})
    const newSourceDialog = (obj) => {
        updateSourceDialog(obj)
    }
    return (
    <p className={classes.sourceName}
    onClick={() => newSourceDialog({open: !sourceDialog.open, content: props.data})}
    >
        {props.sourceName}
        {(sourceDialog.open === true) ? (
            <Dialog 
                maxWidth='lg'
                open={sourceDialog.open}
                onClose={() => newSourceDialog({open: false, content: {}})}>
                    <DialogTitle>
                      <div className={classes.dialogTitle}>
                        <img src={SVBLogo} className={classes.SVicons} alt="SourceView Logo" />
                        <div className={classes.title}>
                          <Typography variant="h5">SourceView Info</Typography>
                        </div>
                      </div>
                    </DialogTitle>
                    <DialogContent dividers className={classes.dialogContent}>
                      <SourceViewInfo content={sourceDialog.content}/>  
                    </DialogContent>
            </Dialog> 
        ) : null}
    </p>
)}

export default SourceName;