import React from 'react'
import SourceIcons from '../../data/SourceIcons.json'
import IconButton from '@material-ui/core/IconButton';
import RssFeed from '@material-ui/icons/RssFeed';
import SVBLogo from '../../images/SVLogo.jpeg'

import { makeStyles } from '@material-ui/core/styles'

const useStyles = makeStyles({
  speakingIcon: {
    transform: 'rotate(45deg)'
  },
  SVicons: {
    height: '50px',
    width: '50px'
  },
  narratorIcon: {
    height: '100px',
    width: '100px'
  },
  SVBInfoTable: {
    width: '100%',
    height: 'fit-content',
    margin: 0,
    padding: 0,
    borderSpacing: 0,
    borderCollapse: 'collapse',
    fontSize: '100%',
  },
  row: {
    width: '100%',
    position: 'relative',
    display: 'flex',
    justifyContent: 'space-between',
  },
  rightCell: {
    wordWrap: 'normal',
    flex: 1,
    textAlign: 'right',
  },
  leftCell: {
    wordWrap: 'break-word',
    flex: 1,
  },
  rightIcon: {
    wordWrap: 'normal',
    flex: 1,
    textAlign: 'center',
  },
  leftIcon: {
    wordWrap: 'break-word',
    flex: 1,
    textAlign: 'center',
  },
  cellTitle: {
    textDecoration: 'underline',
    fontWeight: 'bold',
    marginBottom: '1px',
  },
  narrator: {
    textAlign: 'center'
  },
  sourceName: {
    marginTop: 0,
  }
})

const SourceViewInfo = (props) => {
  const classes = useStyles();
    const SVBData = props.content
    const color = () => {
        switch (SVBData.color) {
            case 'blue':
                return 'darkblue'
            case 'green':
                return '#006400'
            case 'red':
                return '#d60000'
            case 'black':
                return 'black'
            default:
                return 'black'
        } 
    }

    if (SVBData.color !== 'black') {
        let recipList = ``
        SVBData.unique_recipients.map(recip => {
            recipList = (recipList !== '') ? recipList + `, ${recip}` : recipList + `${recip}`
        })
        return (
            <div>
                <table className={classes.SVBInfoTable}>
                    <tbody>
                        <tr className={classes.row}>
                            <td className={classes.leftIcon}><img src={require(`../../images/${SourceIcons[SVBData.source].sourceIcon}.png`)} className={classes.SVicons} alt="Source Name" /></td>
                            <td className={classes.middleIcon}>
                              <IconButton className={classes.speakingIcon} size="large">
                                <RssFeed size="large" />
                              </IconButton>
                            </td>
                            <td className={classes.rightIcon}><img src={require(`../../images/${SourceIcons[SVBData.unique_recipients[0]].sourceIcon}.png`)} className={classes.SVicons} alt="Source Name" /></td>
                        </tr>
                        <tr className={classes.row}>
                            <td className={classes.leftCell}><span className={classes.cellTitle}>SOURCE(S)</span><br />{SVBData.source}</td>
                            <td className={classes.middleCell}></td>
                            <td className={classes.rightCell}><span className={classes.cellTitle}>RECIPIENT(S)</span><br />{(SVBData.unique_recipients.length === 1) ? SVBData.recipient : recipList}</td>
                        </tr>
                        <tr className={classes.row}>
                            <td className={classes.leftCell}>Historical Time Period: </td>
                            <td className={classes.middleCell}></td>
                            <td className={classes.rightCell}>{SVBData.time_period[0]}</td>
                        </tr>
                    </tbody>
                </table>
            </div>
        )
    } else {
        return (<div className={classes.narrator}>
                <img src={require(`../../images/${SourceIcons[SVBData.source].sourceIcon}.png`)} className={classes.narratorIcon} alt="Source Name" />
                <p className={classes.cellTitle}>SOURCE</p>
                <p className={classes.sourceName}>{`${SVBData.source} (${SVBData.unique_sources[0]})`}</p>
                <table className={classes.SVBInfoTable}>
                    <tbody>
                        <tr className={classes.row}>
                            <td className={classes.leftCell}>Historical Time Period: </td>
                            <td className={classes.middleCell}></td>
                            <td className={classes.rightCell}>{SVBData.time_period[0]}</td>
                        </tr>
                    </tbody>
                </table>
            </div>
        )
    }
}

export default SourceViewInfo;