import React, { useEffect, useContext, useState } from 'react'
import axios from 'axios'
import { openDB, deleteDB, wrap, unwrap } from 'idb';
import AppContext from '../contextAPI/AppContext'
import scrollToElement from 'scroll-to-element'
import SpeechBubble from '../components/SpeechBubble/SpeechBubble'
import Introduction from '../components/Introduction/Introduction'
import Narrator from '../components/NarratorText/NarratorText'
import SegmentData from '../components/SegmentData/SegmentData'
import ReadTogether from '../images/SVB-Read-together.png'
import DiscussTogether from '../images/SVB-Discuss-together.png'
import SegmentTitles from '../data/SegmentTitles.json'
import QuestionsRefs from '../data/QuestionsRefs.json'
import QuestionsData from '../data/QuestionsData(Eng).json'
import Button from '@material-ui/core/Button';
import VerbViewDialog from '../components/Dialogs/VerbViewDialog'
import OTNTData from '../components/SegmentData/OTNTData'
import OTNTRefData from '../data/OTNTData.json'
import Divider from '@material-ui/core/Divider';
import Typography from '@material-ui/core/Typography';

import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles(theme => ({
  typography: {
    padding: theme.spacing(2),
    fontSize: 'small',
    color: 'white'
  },
  paper: {
      background: '#3A4C7D',
      width: '75vw'
  },
  segmentDataContainer: {
    display: 'flex',
    justifyContent: 'center',
    margin: '10px 0',
    padding: '10px 0',
  },
  header: {
    display: 'flex',
    justifyContent: 'center',
    flexWrap: 'wrap',
    paddingTop: '5px'
  },
  title: {
    fontSize: '150%',
    fontWeight: ' bold',
    textDecoration: 'underline',
    width: '100%',
    margin: 0,
    textAlign: 'center'
  },
  reference: {
    margin: 0,
    fontSize: '60%',
    color: 'grey'
  },
  discuss: {
    display: 'block',
    textAlign: 'center',
    margin: '10px 0',
    padding: '10px 0',
    borderTop: '0.25px lightgrey solid',
  },
  footer: {
    textAlign: 'center',
    fontSize: '60%',
    margin: '10px 0 30px 0',
    '& p': {
      margin: 0
    }
  },
  images: {
    width: '200px',
    margin: '10px auto',
  },
  readImage: {
    marginBottom: '20px',
    borderBottom: '0.25px lightgrey solid',
  },
  content: {
    margin: '0 0 10px 0',
    padding: '0 0 10px 0'
  },
  questions: {
    textAlign: 'left',
    margin: '30px 0',
    '& p': {
      margin: '10px 0',
    }
  },
  imgCaption: {
    color: 'grey',
    fontSize: '70%',
    margin: '-20px 0 0 0',
    textAlign: 'center'
  },
  groupIcons: {
    opacity: 0.60
  },
  furtherDiscovery: {
    textAlign: 'center',
    margin: '10px'
  }
}))

export default (props) => {
  const classes = useStyles();
  const receivedAppContext = useContext(AppContext);
  console.log(receivedAppContext)
  const hashRefs = (props.location.hash !== '') ? props.location.hash.split('-') : null
  const segID = (hashRefs) ? hashRefs[2] : null
  const book = (hashRefs) ? hashRefs[3] : null
  const view = (hashRefs) ? hashRefs[6] : null
  const [ displayedSegment, updateDisplayedSegment ] = useState(false)
  const [ displayedVersion, updateDisplayedVersion ] = useState(false)
  const [ displayedLanguage, updateDisplayedLanguage ] = useState(false)
  const [ questionRef, updateQuestionRef ] = useState((receivedAppContext.currentSegmentID.substring(0,1) === "S") ? QuestionsRefs[receivedAppContext.currentSegmentID].QRef1 : false)
  const [ loading, updateLoading ] = useState(true)

  async function getSegmentFromIDB(language, version, segment) {
    const db = await openDB(`${language}-${version}`, 1, {
      upgrade(db) {
        const store = db.createObjectStore('segments', {
          keyPath: 'id',
        });
      },
    });
    const value = await db.get('segments', segment)
      .then((val) => {
        if (val === undefined) {
          return null
        } else {
          return val
        }
      })
      .catch(e => {
        console.log(e)
        return null
        })
    return value
  }

  if (!displayedSegment || 
    receivedAppContext.currentSegmentID !== displayedSegment.id ||
    receivedAppContext.version !== displayedVersion ||
    receivedAppContext.language !== displayedLanguage
    ) {
    getSegmentFromIDB(receivedAppContext.language,receivedAppContext.version, (segID !== null) ? segID : receivedAppContext.currentSegmentID)
      .then(segmentData => {
        if (segmentData) {
          if (segmentData.id[0] !== 'I') {
            updateQuestionRef(QuestionsRefs[segmentData.id].QRef1)
          }
          updateDisplayedSegment(segmentData)
          updateDisplayedVersion(receivedAppContext.version)
          updateDisplayedLanguage(receivedAppContext.language)
          updateLoading(false)
          console.log('data came from IDB')
        } else {
          console.log('segment retrieved from server')
          axios.get(`/${receivedAppContext.language}/${receivedAppContext.version}/${(segID !== null) ? segID : receivedAppContext.currentSegmentID}.json`)
          .then(function(response) {
            if (response.data.id[0] !== 'I') {
            updateQuestionRef(QuestionsRefs[response.data.id].QRef1)
          }
            updateDisplayedSegment(response.data)
            updateDisplayedVersion(receivedAppContext.version)
            updateDisplayedLanguage(receivedAppContext.language)
            updateLoading(false)
          })
          .catch(error => console.log(error))
        }
      })
  }

  // This controls the popup for the word information
  const [ verbDialog, updateVerbDialog ] = useState({open: false, content: {}})
  const newVerbDialog = (obj) => {
    updateVerbDialog(obj)
  }

  function turnOnView(v) {
    if (v === 'spheres') {
      receivedAppContext.updateShowSpheres(true)
    } else if (v === 'verbs') {
      receivedAppContext.updateShowVerbs(true)
    } else if (v === 'interlinear') {
      receivedAppContext.updateShowInterlinear(true)
    }
  }

  useEffect(() => {
    if (typeof window !== undefined) {
      if (receivedAppContext.searchID !== '') {
        const id = `#ref-${receivedAppContext.searchID}`
        scrollToElement(id, {
            offset: -65,
            ease: 'inOutCube',
            duration: 1500
        });
        receivedAppContext.updateSearchID('')
      } else if (receivedAppContext.refID !== 'top') {
        const id = `#ref-${receivedAppContext.refID}`
        scrollToElement(id, {
            offset: -65,
            ease: 'inOutCube',
            duration: 1500
        });
        if (view) {
          turnOnView(view)
        }
        receivedAppContext.updateSearchID('')
      } else if (receivedAppContext.refID === 'top') {
      scrollToElement('#topOfPage', {
          offset: -65,
          ease: 'inOutCube',
          duration: 1500
      });
      }
    }
  }, [displayedSegment])
  
  function changeQuestions() {
    console.log(questionRef)
    const questionRefs = [QuestionsRefs[displayedSegment.id].QRef1,QuestionsRefs[displayedSegment.id].QRef2,QuestionsRefs[displayedSegment.id].QRef3]
    console.log(questionRefs)
    const index = questionRefs.indexOf(questionRef)
    console.log(index)
    if (index === 0) {
      const nextRef = QuestionsRefs[displayedSegment.id].QRef2
      updateQuestionRef(nextRef)
    }
    if (index === 1) {
      const nextRef = QuestionsRefs[displayedSegment.id].QRef3
      updateQuestionRef(nextRef)
    }
    if (index === 2) {
      const nextRef = QuestionsRefs[displayedSegment.id].QRef1
      updateQuestionRef(nextRef)
    }
    console.log(questionRef)
  }
  const currentYear = new Date().getFullYear()
  
  const isLoading = (loading && !displayedSegment) || receivedAppContext.segmentID === ''
  
  return (
    <div onMouseUp={() => console.log(window.getSelection())} id='topOfPage'>
      {(isLoading) ? <p>Loading</p> : (
        <div>
          <div className={classes.header}>
            <p className={classes.title}>{SegmentTitles[(receivedAppContext.currentSegmentID) ? receivedAppContext.currentSegmentID : 'S285'].title}</p>
          </div>
          {((displayedSegment.id)[0] === 'S' && !isLoading) ? (
            <div>
              <div className={classes.segmentDataContainer}>
                <SegmentData 
                  colors={displayedSegment.colors}
                  sources={displayedSegment.sources}
                  repeatedWords={displayedSegment.repeatedWords}
                />
              </div>
              <div className={`${classes.images} ${classes.readImage}`}>
                <img src={ReadTogether} className={classes.groupIcons} alt="Read Together" />
                <p className={classes.imgCaption}>Each person reads a color aloud</p>
              </div>
              <div className={classes.content}>
                {displayedSegment.content.map(block => {
                  if (block.data.color === 'black') {
                    return (<Narrator 
                      sourceData={block.data}
                      inlines={block.nodes}
                      verbViewDialog={newVerbDialog} />
                      )
                  } else {
                    return (<SpeechBubble 
                      sourceData={block.data}
                      inlines={block.nodes}
                      verbViewDialog={newVerbDialog} />
                      )
                  }
                })}
              </div>
              {(verbDialog.open === true) ? (
                  <VerbViewDialog verbDialog={verbDialog} newVerbDialog={newVerbDialog} /> 
              ) : null}
              <div className={classes.discuss}>
                <div className={classes.images}>
                  <img src={DiscussTogether} alt="Discuss Together" />
                  <p className={classes.imgCaption}>Discuss the questions with others</p>
                </div>
                <p className={classes.title}>{QuestionsData[questionRef].title}</p>
                <div className={classes.questions}>
                  <p>{QuestionsData[questionRef].Q1}</p>
                  <p>{QuestionsData[questionRef].Q2}</p>
                  <p>{QuestionsData[questionRef].Q3}</p>
                  <p>{QuestionsData[questionRef].Q4}</p>
                </div>
                <Button color='secondary' variant="contained" onClick={() => changeQuestions()}>Change Questions</Button>
              </div>
              <Divider />
              {(receivedAppContext.version === 'NLT' && Object.keys(OTNTRefData).indexOf(receivedAppContext.currentSegmentID) !== -1) ? (<div className={classes.furtherDiscovery}>
                <Typography variant="h4">Further Discovery</Typography>
                <OTNTData segmentID={receivedAppContext.currentSegmentID}/>
              </div>) : null}
              <div className={classes.footer}>
                <p>{receivedAppContext.AvailableContent[receivedAppContext.language].versions[receivedAppContext.version].copyright}</p>
                <br/>
                <p> {currentYear} SourceView LLC.</p>
              </div>
            </div>
          ) : (
            <div>
              <Introduction 
                content={displayedSegment.content.document.nodes} 
                segmentID={receivedAppContext.currentSegmentID}
                book={receivedAppContext.currentBook}
              />
              <div className={classes.footer}>
                <p>{receivedAppContext.AvailableContent[receivedAppContext.language].versions[receivedAppContext.version].copyright}</p>
                <br/>
                <p> {currentYear} SourceView LLC.</p>
              </div>
            </div>
          )}
        </div>
      )}
    </div>
  )}